import Vue from "vue";
import Router from "vue-router";
import Home from "./components/Home.vue";
import Cases from "./components/Cases.vue";
import Contacts from "./components/Contacts.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/cases",
      name: "cases",
      component: Cases
    },
    {
      path: "/contacts",
      name: "contacts",
      component: Contacts
    }
  ]
});
