<template>
  <div>
    <router-link
      class="case"
      to="/"
    >
      <div class="img-wrap">
        <div
          class="img"
          :style="{ 'background-image': 'url(' + src + ')' }"
        ></div>
      </div>
      <div>
        <h2>{{ project.fields.title }}</h2>
        <p>{{ project.fields.text }}</p>
      </div>
    </router-link>
  </div>
</template>

<script>
import Vue from "vue";
import api from "../api";

export default {
  name: "Case",
  props: {
    project: Object
  },
  data: () => ({
    src: ""
  }),
  created() {
    Vue.http
      .get(
        `${api.assets}/${this.project.fields.img.sys.id}?access_token=${
          api.accessToken
        }`
      )
      .then(response => {
        this.src = response.body.fields.file.url;
      });
  }
};
</script>


<style lang="sass" scoped>
a
  margin: 0
  &,
  &:active,
  &:focus,
  &:visited
    color: #fff
    text-decoration: none
h2
  font-family: 'ObjectSans-Regular'
  font-size: 6vw
  margin: 0
p
  font-family: 'ObjectSans-Regular'
  font-size: 1vw
.case
  display: flex
  align-items: flex-start
  margin-bottom: 8vw
  @media (max-width: 1300px)
    font-size: 18px
.img-wrap
  position: relative
  min-width: 40%
  padding-bottom: 56.25%
  margin-right: 4vw
.img
  position: absolute
  top: 0
  left: 0
  right: 0
  bottom: 0
  width: 100%
  height: 100%
  background-position: center center
  background-size: cover
  background-repeat: no-repeat
</style>
