import Vue from "vue";
import VueResource from "vue-resource";

import App from "./App.vue";
import router from "./router";

import "./assets/fonts.sass";

Vue.use(VueResource);
Vue.http.options.emulateJSON = true;
Vue.config.productionTip = false;

new Vue({
  router,
  render: h => h(App)
}).$mount("#app");
