<template>
  <div>
    <h1>Cases</h1>

    <div class="cases">
      <Case
        v-for="(project, index) in cases"
        :key="index"
        :project="project"
      ></Case>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import Case from "./Case";
import api from "../api";

export default {
  name: "Cases",
  components: {
    Case: Case
  },
  data: () => ({
    cases: []
  }),
  created() {
    return Vue.http.get(api.entries).then(response => {
      this.cases = response.data.items;
    });
  }
};
</script>

<style lang="sass" scoped>
h1
  font-family: 'ObjectSans-Heavy'
  margin-bottom: 2em
</style>

